

.blueLabel {
    color: var(--blue);
}

.redLabel {
    color: var(--red);
}

.field {
    position: relative;
    margin: 0;
    padding: 0.4rem 0.4rem;
    border-radius: 0.8rem;
    width: 25.1rem;
    border: 1px solid var(--grey51);
}

.fullwidth {
    width: 99%;
}

.field:hover {
    border: 1px solid var(--grey20);
}

.blueBorder {
    border: 1px solid var(--blue);
}

.fieldDisabled {
    background-color: var(--grey95);
}

.redBorder {
    border: 1px solid var(--red);
}

input, textarea {
    border: 0;
    width: 94%;
    padding: 1.4rem 0.8rem;
    font-family: 'Noto Sans', sans-serif;
    font-weight: 500;
    font-size: 1.4rem;
    color: var(--grey20);
}

textarea {
    resize: none;
}

input:focus, textarea:focus {
    outline: none;
}

.trimStart {
    width: 85%;
    margin-left: 2.5rem;
}

.trimEnd {
    width: 85%;
}

.verticalTrim {
    padding: 0.6rem 0.8rem;
}

span {
    top: 1.8rem;
    position: absolute;
}

.shiftLeft {
    right: 1rem;
}

.shiftRight {
    left: 1rem;
}

.helperText {
    font-family: 'Noto Sans', sans-serif;
    font-size: 1rem;
    font-weight: 400;
}